import React, { Component } from 'react';
import { withRouter } from 'react-router';
import CanGokceaslanSubMinified from "@assets/webp/sub-image-cropped-final-minified1.webp";
import './index.scss';

const sWidth: number = window.innerWidth;
function About(props: {
    hideTitle?: boolean
}) {
    return (
        <section className="about-section">
            <div className="container about-top-container">
                {!props?.hideTitle && (
                    <div className="about-top-row">
                        <div className="section-header">
                            <h2>Hakkımda</h2>
                        </div>

                    </div>
                )}
                {props?.hideTitle && (
                    <div className="about-top-row" style={{ marginTop: -20, height: 0 }}>
                    </div>
                )}
                <div className="row about-bottom-row" style={{
                    ...props?.hideTitle && (
                        sWidth < 992 && {
                            marginTop: "-65px"
                        }
                    )
                }}>
                    <div className="col-xl-5 col-lg-12 col-md-12">
                        <div className="image-container">
                            <img
                                src={CanGokceaslanSubMinified}
                                alt="Can Gökçeaslan Uzun Fotoğraf"
                                className="img-fluid"
                            />
                        </div>
                    </div>

                    <div className="col-xl-7 col-lg-12 col-md-12">
                        <div className="about-text-container">
                            <div className="about-text">
                                <p>Boğaziçi Üniversitesi Fizik bölümünden 2019 yılında mezun oldum. Mezun olmadan önce de çalışmalarına devam ettiğimiz ve benim kurucu ortak olarak bulunduğum girişimimiz <a href="https://www.barty.app" target="_blank"><strong>Barty</strong></a> an itibariyle <a target="_blank" href="https://webrazzi.com/arama/?q=barty"><strong>2 adet yatırım turunu</strong></a> tamamlamış bulunuyor. Girişimimizin tüm teknolojik aşamalarının içerisinde bizzat bulundum. Bu sebep ile kısa bir süreç içerisinde çok değerli ve önemli bilgiler edindim. Bunun yanında bu aşamaları düzenli bir şekilde araştırmak ve geliştirmenin ne kadar önemli olduğunu kavradım. Bu benim hayatımdaki temel taşlardan birisi oldu diyebilirim. Barty benim ilk girişimim ve bu girişim için 2.5 senedir dur durak bilmeden çalışıyorum. Bu sebep ile girişimlerle ilgili olarak kendime bir yol haritası hazırladım. Bu yol haritasının nihai amacı insanların optimize edilmemiş problemlerini bulup teknoloji vasıtasıyla bu problemleri önce ülkemize daha sonrasında da dünyaya faydalı olacak şekilde çözmektir. </p>
                                <p>Kişisel merakım ve araştırma hevesim her seferinde beni rahatsız edecek bir konu buluyor. Keşfettiğim şeyleri insanlarla paylaşmak benim için bir hobi. Bu hobiyi gerçekleştirebilmek için bir yandan <a target="_blank" href="https://www.cangokceaslan.com/social"><strong>içerik üreticiliği</strong></a> yapıyorum. Özellikle <a target="_blank" href="https://www.udemy.com/user/cangokceaslan"><strong>Udemy</strong></a>  üzerinde paylaştığım kurslar sayesinde şu anda toplamda 55 bin kişiye hitap ediyorum. Bunu günün birinde tüm dünyadaki teknoloji ve yazılım ile ilgilenen bir kitleye yapabilmek benim yol haritamda ulaşmak istediğim en önemli noktalardan birisidir.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default withRouter(About);